.App {
  text-align: center;
}

.jumbo{
  opacity: .80;
}

#jumbo-style{
  background-color: rgb(36, 36, 36);
}

body{
  background-image: url("https://wallpapercave.com/wp/INbbeXh.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

html{
  background-color: black;
}

.card-names {
  font-size: 18px;
}

h3{
  margin: 10px;
  right: 20px;
  font-family: 'Trade Winds';
}

.Easy{
  color: green;
}
.Medium{
  color: rgb(241, 113, 28);
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}
.Hard{
  color: red;
  text-shadow: -2px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

.my-nav{
  height: 60px;
}

.giphy-embed{
  height: 60px;
}

.main-text{
  font-family: 'Trade Winds';
  color: rgb(241, 78, 28);
  font-size: 35px;
  margin-left: 10px;
  text-shadow: -2px 0 black, 0 2px black, 2px 0 black, 0 -2px black;
}

.card-whole{
  margin: 10px;
}

.card-whole :hover{
  cursor: crosshair;
}

.fighter{
  font-family: 'Trade Winds';
}

.false{
  display: none;
}

.true{
  display: inline-block;
  position: absolute;
  top: 61%;
}